import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BaseServiceService } from '@app/services/base-service.service';
import Axios from 'axios';
import { Subject } from 'rxjs';
import Swal from 'sweetalert2';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.scss']
})
export class StatusComponent implements OnInit {
  model: NgbDateStruct;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  status:any={}
  statusNames: any;
  tableStatus:boolean=true;
  closeResult = '';
  selectedObj:any = {};
  constructor(private router: Router, private baseService: BaseServiceService,config: NgbModalConfig, private modalService: NgbModal) { 
    this.getStatusNames();
  }

  formSubmit(form:NgForm) {
   
    Axios.post(
      this.baseService.baseUrl + 'secure/status/addStatus', this.status,
      {
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload(); });
         form.resetForm('');
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  getStatusNames() {
    Axios.get(this.baseService.baseUrl + 'secure/status/allStatus',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
    })
      .then(response => { 
        if (response.data.status) {
          this.statusNames = response.data.data;
          console.log(this.statusNames);
          console.log(response.data);
          if(this.tableStatus)
          {
            this.tableStatus=false;
          this.dtTrigger.next();
          }
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      }) 
  }

  open(content,selObj) {
    console.log(selObj);
    this.selectedObj = selObj;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log("LLLLLLL",result);
      Axios.put(this.baseService.baseUrl + 'secure/status/update_status/'+result._id,result,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
        
      })
        .then(response => {        
          console.log(response)
          if (response.data. status) {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
          // .then(() => {
          //   location.reload(); });
          this.getStatusNames();
          }
          else if(this.baseService.checkToken(response.data.message , response.data.status))
          {
            
          }
          else{
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
           }
        })
        .catch(function (error) {
          console.log(error)
        })
    }, (reason) => {
      console.log("OOOOOOOOOOo",);
      
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  deleteDept(id)
  {
    if(confirm("Are You Sure To Delete This Record"))
    {
    Axios.delete(this.baseService.baseUrl + 'secure/status/delete_status/'+id,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {        
        console.log(response)
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload(); });
        this.getStatusNames();
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
    }
  }
  
  ngOnInit() {
  }

}
