import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { BaseServiceService } from '@app/services/base-service.service';
import Axios from 'axios';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import Swal from 'sweetalert2';
declare var $:any;
@Component({
  selector: 'app-dept',
  templateUrl: './dept.component.html',
  styleUrls: ['./dept.component.scss']
})
export class DeptComponent implements OnInit {
  model: NgbDateStruct;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  deptNames: any;
  dept_name_edit:any='';
  tableStatus:boolean=true;
  dept:any={};
  closeResult = '';
  selectedFiles: FileList;
  mainDeptNames:any;
  convertedObj: any = '';
  sel:boolean = false;
  selectedObj:any = {};
  selectedObj1:any = {};
  constructor(private router: Router, private baseService: BaseServiceService,config: NgbModalConfig, private modalService: NgbModal) {     
    this.getMainDept();
    this.getDept();
    

  }
  formSubmit() {
   
    Axios.post(
      this.baseService.baseUrl + 'secure/department/add_dept', this.dept,
      {
        headers: {
          'x-auth' : this.baseService.authToken
        },
      
      })
      .then(response => {
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        }).then(() => {
          location.reload(); });
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

 /////////Get Main Dept
 getMainDept() {
  console.log("Dept");
  Axios.get(this.baseService.baseUrl + 'secure/mainDept/allMainDept',{
    headers: {
      'x-auth' : this.baseService.authToken
    },
    
  })
    .then(response => {        
     
      if (response.data.status) {
        console.log("Issue List");
        this.mainDeptNames = response.data.data;
      }
      else if(this.baseService.checkToken(response.data.message , response.data.status))
      {
        
      }
      else{
        Swal.fire({
          position: 'center',
          icon: 'error',
          title: response.data["message"],
          showConfirmButton: false,
          timer: 3000
      })
       }
    })
    .catch(function (error) {
      console.log(error)
    })
}


  getDept() {
    console.log("Dept");
    Axios.get(this.baseService.baseUrl + 'secure/department/get_dept',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
       
        if (response.data.status) {
          console.log("Issue List");
          this.deptNames = response.data.data;
          console.log(this.deptNames);
          console.log(response.data);
          if(this.tableStatus)
          {
            this.dtTrigger.next();
            this.tableStatus=false;
          }
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  open(content,selObj) {
    console.log(selObj);
    this.selectedObj = selObj;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log("LLLLLLL",result);
      Axios.put(this.baseService.baseUrl + 'secure/department/update_dept/'+result._id,result,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
        
      })
        .then(response => {        
          console.log(response)
          if (response.data. status) {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
          this.getDept();
          }
          else if(this.baseService.checkToken(response.data.message , response.data.status))
          {
            
          }
          else{
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
           }
        })
        .catch(function (error) {
          console.log(error)
        })
    }, (reason) => {
      console.log("OOOOOOOOOOo",);
      
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  /////////Main Dept

  openDept(content) {
    // console.log(selObj);
    // this.selectedObj = selObj;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log("LLLLLLL",result);
      Axios.post(this.baseService.baseUrl + 'secure/mainDept/addMainDept',this.selectedObj1,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
        
      })
        .then(response => {        
          console.log(response)
          if (response.data.status) {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
          this.getMainDept();
          }
          else if(this.baseService.checkToken(response.data.message , response.data.status))
          {
            
          }
          else{
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
           }
        })
        .catch(function (error) {
          console.log(error)
        })
    }, (reason) => {
      console.log("OOOOOOOOOOo",);
      
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }



  deleteDept(id)
  {
    if(confirm("Are You Sure To Delete This Record"))
    {
    Axios.delete(this.baseService.baseUrl + 'secure/department/delete_dept/'+id,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {        
        console.log(response)
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload(); });
        this.getDept();
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
    }

  }

  convert(objArray){
    // console.log(objArray);
    this.sel = true;
    this.convertedObj = JSON.stringify(objArray, null, 2);
   }
   onError(err){
     console.log(err);
     this.convertedObj = err;
    //  this.toaster.error(err)
   }

   save() {
    let finalObj = JSON.parse(this.convertedObj);
    console.log(finalObj);
    Axios.post(this.baseService.baseUrl + 'secure/department/csv', finalObj,{
		  headers: {
        'x-auth' : this.baseService.authToken
      },
		})
			.then(response => {
      console.log(response);
	       if(response.data.status){
			  //  this.toaster.success(response.data.message)
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: response.data["message"],
          showConfirmButton: false,
          timer: 3000
      })
		   }else {
			    // this.toaster.error(response.data.message)
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
		   }
			   
			   
			}).catch(function (error) {
				// handle error
				console.log(error);
			})
			.finally(function () {
				// always executed
			});
}





  ngOnInit() {
    
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10
    };
  }

}
