import { NgModule,CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';
import { TagsComponent } from './tags/tags.component';
import { StatusComponent } from './status/status.component';
import { DataTablesModule } from 'angular-datatables';
import { AssignComponent } from './assign/assign.component';
import { IssuesComponent } from './issues/issues.component';
import { UserComponent } from './user/user.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { DeptComponent } from './dept/dept.component';
import { UserListComponent } from './user-list/user-list.component';
import { SignOutComponent } from './sign-out/sign-out.component';
import { StoreComponent } from './store/store.component';
import { EditUserComponent } from './edit-user/edit-user.component';
import {NgxCSVtoJSONModule} from 'ngx-csvto-json';
import { InactiveUserlistComponent } from './inactive-userlist/inactive-userlist.component';

const appsRoutes: Routes = [
  {
    path: 'tag-entry',
    component: TagsComponent
  },
  {
    path: 'status-entry',
    component: StatusComponent
  },
  {
    path: 'assign-entry',
    component: AssignComponent
  },
  {
    path: 'department-entry',
    component: DeptComponent
  },
  {
    path: 'issue-entry',
    component: IssuesComponent
  },
  {
    path: 'add-user',
    component: UserComponent
  },
  {
    path: 'add-store',
    component: StoreComponent
  },
  {
    path: 'user-list',
    component: UserListComponent
  },
  {
    path: 'inactive-users',
    component: InactiveUserlistComponent
  },
  {
    path: 'user-edit',
    component: EditUserComponent
  },
  {
    path: 'signout',
    component: SignOutComponent
  },
];

@NgModule({
  declarations: [TagsComponent, StatusComponent, AssignComponent, IssuesComponent, UserComponent, DeptComponent, UserListComponent, SignOutComponent, StoreComponent, EditUserComponent, InactiveUserlistComponent],
  imports: [
    CommonModule,
    NgxPaginationModule,
    ReactiveFormsModule,
    AngularMultiSelectModule,
    NgxCSVtoJSONModule,
    FormsModule,
    DataTablesModule,
   
    RouterModule.forChild(appsRoutes)
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ],
  exports:[TagsComponent, StatusComponent, SignOutComponent,StoreComponent, AssignComponent,IssuesComponent,DeptComponent,UserComponent,UserListComponent,SignOutComponent,EditUserComponent,InactiveUserlistComponent]
})
export class MasterDataModule { }
