import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BaseServiceService } from '@app/services/base-service.service';
import Axios from 'axios';
import { Subject } from 'rxjs';
import Swal from 'sweetalert2';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-tags',
  templateUrl: './tags.component.html',
  styleUrls: ['./tags.component.scss']
})
export class TagsComponent implements OnInit {
  model: NgbDateStruct;
  dtOptions: DataTables.Settings = {};
  closeResult = '';
  selectedObj:any = {};
  dtTrigger: Subject<any> = new Subject<any>();
  tag:any={}
  tagNames: any=[];
  constructor(private router: Router, private baseService: BaseServiceService,config: NgbModalConfig, private modalService: NgbModal) { }

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10
    };
   this.getTags(); 
  }

  formSubmit(form:NgForm) {
   console.log(this.baseService.authToken)
    Axios.post(
      this.baseService.baseUrl + 'secure/tags/addTag_Name', this.tag,
      {
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {
        console.log(response);
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload();  });  //
        form.resetForm('');
        this.getTags();
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }
  getTags() {
    Axios.get(this.baseService.baseUrl + 'secure/tags/allTags',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
        if (response.data.status) {
          this.tagNames = response.data.data;
          console.log(this.tagNames);
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  open(content,selObj) {
    console.log(selObj);
    this.selectedObj = selObj;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log("LLLLLLL",result);
      Axios.put(this.baseService.baseUrl + 'secure/tags/update_tags/'+result._id,result,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
        
      })
        .then(response => {        
          console.log(response)
          if (response.data. status) {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
          // .then(() => {
          //   location.reload(); });
          this.getTags();
          }
          else if(this.baseService.checkToken(response.data.message , response.data.status))
          {
            
          }
          else{
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
           }
        })
        .catch(function (error) {
          console.log(error)
        })
    }, (reason) => {
      console.log("OOOOOOOOOOo",);
      
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  deleteDept(id)
  {
    if(confirm("Are You Sure To Delete This Record"))
    {
    Axios.delete(this.baseService.baseUrl + 'secure/tags/delete_tags/'+id,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {        
        console.log(response)
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload(); });
        this.getTags();
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
    }

  }




}
