import { Component, OnInit } from '@angular/core';
import { BaseServiceService } from '@app/services/base-service.service';
import { Router } from '@angular/router';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import Swal from 'sweetalert2';
import { ChangeDetectorRef } from '@angular/core';
import Axios from 'axios';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  model: NgbDateStruct;
  csvStatus:boolean = false;
  newUser:any = {
    user_type:"Corporate"
  };
  roleStatus:boolean=true;
  deptNames: any;
  storeNames: any;
  dropdownList = [];
  formModel = {
    "selectedItems": []
  };
	dropdownSettings = {};
  // selectedFiles: FileList;
  convertedObj: any = '';
  sel:boolean = false;
  constructor(private router: Router, private cdRef:ChangeDetectorRef, private baseService:BaseServiceService) { 
    this.dropdownSettings = {
			singleSelection: true,
			text: "Select Menu List",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			classes: "myclass custom-class"
    };
    this.getDept();
    this.getStore();
  }

  getDept() {
    console.log("Dept");
    Axios.get(this.baseService.baseUrl + 'secure/department/get_dept',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
       
        if (response.data.status) {
          console.log("Issue List");
          this.deptNames = response.data.data;
          console.log(this.deptNames);
          console.log(response.data);
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  getStore() {
    console.log("Store");
    Axios.get(this.baseService.baseUrl + 'secure/store/allStore',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
       
        if (response.data.status) {
          console.log("Issue List");
          this.storeNames = response.data.data;
          let tmp = [];
          for(let i=0; i < this.storeNames .length; i++) {
            tmp.push({ id: this.storeNames[i]._id, itemName: this.storeNames[i].store_name });
          }
          this.dropdownList = tmp;
        console.log("DropDown Data");
        console.log(this.dropdownList);
          console.log(this.storeNames);
          console.log(response.data);
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  
  convert(objArray){
    // console.log(objArray);
    this.sel = true;
    this.convertedObj = JSON.stringify(objArray, null, 2);
   }
   onError(err){
     console.log(err);
     this.convertedObj = err;
    //  this.toaster.error(err)
   }

   save() {
    let finalObj = JSON.parse(this.convertedObj);
    console.log(finalObj.store_id);
    console.log(finalObj);
    Axios.post(this.baseService.baseUrl + 'secure/users/usercsv', finalObj,{
		  headers: {
        'x-auth' : this.baseService.authToken
      },
		})
			.then(response => {
      console.log(response);
	       if(response.data.status){
			  //  this.toaster.success(response.data.message)
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: response.data["message"],
          showConfirmButton: false,
          timer: 3000
      })
		   }else {
			    // this.toaster.error(response.data.message)
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
		   }
			   
			   
			}).catch(function (error) {
				// handle error
				console.log(error);
			})
			.finally(function () {
				// always executed
			});
}


  showCsv()
  {
    this.csvStatus=!this.csvStatus;
  }

  userTypeData(data)
  {
   if(data=="Store")
   {
    this.roleStatus=false;
    this.newUser.user_type="Store";
    // this.newUser.role={
    //   isAdmin:false,
    //   isUser:true
    // }
   }
   else if(data == "Corporate")
   {
    this.roleStatus=true;
    this.newUser.user_type="Corporate";
   }
   else
   {
    this.dropdownSettings = {
			singleSelection: false,
			text: "Select Menu List",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			classes: "myclass custom-class"
    };
    this.newUser.user_type="Sales";
   }
  }

  ngAfterViewChecked()
  {
    this.cdRef.detectChanges();
  }

  formSubmit(form:NgForm)
  {
    this.newUser.store_id=[];
    for(let i=0;i<this.formModel.selectedItems.length;i++)
    {
      this.newUser.store_id.push(this.formModel.selectedItems[i].id)
    }
    console.log(this.newUser);
    Axios.post(this.baseService.baseUrl + 'secure/users/add_User',this.newUser,{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
        console.log(response)
        if (response.data. status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload();
        //  });
         form.resetForm("");
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }



  ngOnInit(): void {
  }

  onItemSelect(item: any) {
    console.log("on Select Item...");
    console.log(this.formModel.selectedItems);
    // for(let i=0;i<this.formModel.selectedItems.length;i++)
    // {
    // this.newTicket.tags.push(this.formModel.selectedItems[i].id);
    // }
    console.log("Tag Names");
    // this.newTicket.tags.push(this.formModel.selectedItems)
		// console.log(this.formModel.selectedItems);
	}
	OnItemDeSelect(item: any) {
		console.log(item);
		// console.log(this.formModel.selectedItems);
	}
	onSelectAll(items: any) {
    console.log("On Select All");
    console.log(this.formModel.selectedItems);
    // this.tags.push(this.formModel.selectedItems);
	}
	onDeSelectAll(items: any) {
		console.log(items);
	}

}
