import { Component, OnInit } from '@angular/core';
import { BaseServiceService } from '@app/services/base-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ChangeDetectorRef } from '@angular/core';
import Swal from 'sweetalert2';
import Axios from 'axios';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.scss']
})
export class EditUserComponent implements OnInit {
  model: NgbDateStruct;
  newUser:any = {
  };
  deptNames: any;
  storeNames: any;
  dropdownList = [];
  formModel = {
    "selectedItems": []
  };
	dropdownSettings = {};
  id: any;
  roleStatus: boolean = false;
  constructor(private router: Router,private _router:ActivatedRoute,  private cdRef:ChangeDetectorRef,private baseService:BaseServiceService) { 
    this.id = this._router.snapshot.queryParamMap.get('id');
    this.dropdownSettings = {
			singleSelection: true,
			text: "Select Menu List",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			classes: "myclass custom-class"
    };
    this.getDept();
    this.getStore();
  }

  getUserById() {
    console.log("userBy ID");
    Axios.get(this.baseService.baseUrl + 'secure/users/user_data/'+this.id,{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
       console.log("Testing");
       console.log(response);
        if (response.data.status)
         {
          console.log("Issue List");
          this.newUser = response.data.data;
          let tmp = [];
          for (let i = 0; i < this.newUser.store_id.length; i++) {
             this.storeNames.filter(store=>{
                if(store._id == this.newUser.store_id[i])
                tmp.push({ id: store._id, itemName: store.store_name });
             })
          }
          this.formModel.selectedItems = tmp;
          console.log( this.formModel.selectedItems);
          console.log(response.data);
          if(this.newUser.user_type == 'Corporate')
          this.roleStatus = false;
          else if(this.newUser.user_type == 'Sales')
          {
            this.dropdownSettings = {
              singleSelection: false,
              text: "Select Menu List",
              selectAllText: 'Select All',
              unSelectAllText: 'UnSelect All',
              enableSearchFilter: true,
              classes: "myclass custom-class"
          }
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
        }
         })
      .catch(function (error) {
        console.log(error)
      })
  }

  userTypeData(data)
  {
   if(data=="Store")
   {
    this.roleStatus=true;
    this.newUser.user_type="Store"
    // this.newUser.role={
    //   isAdmin:false,
    //   isUser:true
    // }
   }
   else if(data == "Corporate")
   {
    this.roleStatus=false;
    this.newUser.user_type="Corporate";
   }
   else
   {
    this.dropdownSettings = {
			singleSelection: false,
			text: "Select Menu List",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			classes: "myclass custom-class"
    };
    this.newUser.user_type="Sales";
   }
  }

  getDept() {
    console.log("Dept");
    Axios.get(this.baseService.baseUrl + 'secure/department/get_dept',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
    })
      .then(response => {        
       
        if (response.data.status) {
          console.log("Issue List");
          this.deptNames = response.data.data;
          console.log(this.deptNames);
          console.log(response.data);
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  getStore() {
    console.log("Store");
    Axios.get(this.baseService.baseUrl + 'secure/store/allStore',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
        if (response.data.status) {
          console.log("Issue List");
          this.storeNames = response.data.data;
          this.storeNames = response.data.data;
          this.getUserById();
          let tmp = [];
          for(let i=0; i < this.storeNames .length; i++) {
            tmp.push({ id: this.storeNames[i]._id, itemName: this.storeNames[i].store_name });
          }
          this.dropdownList = tmp;
          console.log(this.storeNames);
          console.log(response.data);
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  formSubmit()
  {
    this.newUser.store_id=[];
    for(let i=0;i<this.formModel.selectedItems.length;i++)
    {
      this.newUser.store_id.push(this.formModel.selectedItems[i].id)
    }
    console.log(this.newUser);
    Axios.put(this.baseService.baseUrl + 'secure/users/user_data/'+this.id,this.newUser,{
      headers: {
        'x-auth' : this.baseService.authToken
      },
      
    })
      .then(response => {        
        console.log(response)
        if (response.data. status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        this.getUserById();
        // .then(() => {
        //   location.reload(); });
        }
        // else if(this.baseService.checkToken(response.data.message , response.data.status))
        // {
          
        // }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }


  ngOnInit(): void {
  }

  ngAfterViewChecked()
  {
    this.cdRef.detectChanges();
  }

  
  onItemSelect(item: any) {
    console.log("on Select Item...");
    console.log(this.formModel.selectedItems);
    // for(let i=0;i<this.formModel.selectedItems.length;i++)
    // {
    // this.newTicket.tags.push(this.formModel.selectedItems[i].id);
    // }
    console.log("Tag Names");
    // this.newTicket.tags.push(this.formModel.selectedItems)
		// console.log(this.formModel.selectedItems);
	}
	OnItemDeSelect(item: any) {
		console.log(item);
		// console.log(this.formModel.selectedItems);
	}
	onSelectAll(items: any) {
    console.log("On Select All");
    console.log(this.formModel.selectedItems);
    // this.tags.push(this.formModel.selectedItems);
	}
	onDeSelectAll(items: any) {
		console.log(items);
	}

}
