import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { BaseServiceService } from '@app/services/base-service.service';
import { Subject } from 'rxjs';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import Axios from 'axios';
import Swal from 'sweetalert2';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-issues',
  templateUrl: './issues.component.html',
  styleUrls: ['./issues.component.scss']
})
export class IssuesComponent implements OnInit {
  model: NgbDateStruct;
  dept:any={}
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  issue:any={}
  mainDeptNames:any;
  tableStatus:boolean=true;
  deptNames: any;
  issuesNames:any;
  closeResult = '';
  selectedObj:any = {};
  constructor(private router: Router, private baseService: BaseServiceService,config: NgbModalConfig, private modalService: NgbModal) {
    this.getDept();
     this.getIssues();
     this.getMainDept();
   }
  formSubmit(form:NgForm) {
    Axios.post(
      this.baseService.baseUrl + 'secure/typeofIssue/add_issue', this.issue,
      {
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
            
        })
        // .then(() => {
        //   location.reload(); });
        form.resetForm("");
        this.getIssues();
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

 /////////Get Main Dept
 getMainDept() {
  console.log("Dept");
  Axios.get(this.baseService.baseUrl + 'secure/mainDept/allMainDept',{
    headers: {
      'x-auth' : this.baseService.authToken
    },
    
  })
    .then(response => {        
     
      if (response.data.status) {
        console.log("Issue List");
        this.mainDeptNames = response.data.data;
      }
      else if(this.baseService.checkToken(response.data.message , response.data.status))
      {
        
      }
      else{
        Swal.fire({
          position: 'center',
          icon: 'error',
          title: response.data["message"],
          showConfirmButton: false,
          timer: 3000
      })
       }
    })
    .catch(function (error) {
      console.log(error)
    })
}

  getDept() {
    Axios.get(this.baseService.baseUrl + 'secure/department/get_dept',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
    })
      .then(response => {        
       
        if (response.data.status) {
          console.log("Dept List");
          this.deptNames = response.data.data;
          console.log(this.deptNames);
          console.log(response.data);
          
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  getIssues() {
    Axios.get(this.baseService.baseUrl + 'secure/typeofIssue/get_issue',{
      headers: {
        'x-auth' : this.baseService.authToken
      },
    })
      .then(response => {        
       
        if (response.status) {
          this.issuesNames = response.data.data;
          console.log(this.issuesNames);
          console.log(response.data);
          if(this.tableStatus)
          {
            this.dtTrigger.next();
                 this.tableStatus=false;
          }
        }
        else if(this.baseService.checkToken(response.data.message , response.data.status))
        {
          
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  open(content,selObj) {
    console.log(selObj);
    this.selectedObj = selObj;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log("LLLLLLL",result);
      Axios.put(this.baseService.baseUrl + 'secure/typeofIssue/update_issue/'+result._id,result,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
        
      })
        .then(response => {        
          console.log(response)
          if (response.data. status) {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
          // .then(() => {
          //   location.reload(); });
          this.getIssues();
          }
          else if(this.baseService.checkToken(response.data.message , response.data.status))
          {
            
          }
          else{
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
           }
        })
        .catch(function (error) {
          console.log(error)
        })
    }, (reason) => {
      console.log("OOOOOOOOOOo",);
      
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  deleteDept(id)
  {
    if(confirm("Are You Sure To Delete This Record"))
    {
    Axios.delete(this.baseService.baseUrl + 'secure/typeofIssue/delete_issue/'+id,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {        
        console.log(response)
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        // .then(() => {
        //   location.reload(); });
        this.getIssues();
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
    }

  }




  ngOnInit(): void {
  }

}
