import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { BaseServiceService } from '@app/services/base-service.service';
import Axios from 'axios';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import Swal from 'sweetalert2';
import { Subject } from 'rxjs';
@Component({
  selector: 'app-assign',
  templateUrl: './assign.component.html',
  styleUrls: ['./assign.component.scss']
})
export class AssignComponent implements OnInit {
  model: NgbDateStruct;
  assign:any={};
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  assignList: any;
  authToken: string;
  closeResult = '';
  selectedObj:any = {};
  constructor(private router: Router, private baseService: BaseServiceService,config: NgbModalConfig, private modalService: NgbModal) { 
  
  }

  ngOnInit(): void {
    this.getAssignList();
  }

  formSubmit() {
   
    Axios.post(
      this.baseService.baseUrl + 'secure/assign/addAssignName', this.assign,
      {
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        }).then(() => {
          location.reload(); });
        }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  getAssignList() {
    Axios.get(this.baseService.baseUrl + 'secure/assign/getAssignName',{
      headers: {
        'x-auth' : this.baseService.authToken
      },      
    })
      .then(response => {        
        if (response.status) {
          this.assignList = response.data.data;
          console.log("Assign List");
          
          console.log(this.assignList);
          console.log(response.data);
        }
      })
      .catch(function (error) {
        console.log(error)
      })
  }

  open(content,selObj) {
    console.log(selObj);
    this.selectedObj = selObj;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      console.log("LLLLLLL",result);
      Axios.put(this.baseService.baseUrl + 'secure/assign/update_assign/'+result._id,result,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
        
      })
        .then(response => {        
          console.log(response)
          if (response.data. status) {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          }).then(() => {
            location.reload(); });
          }
          else if(this.baseService.checkToken(response.data.message , response.data.status))
          {
            
          }
          else{
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: response.data["message"],
              showConfirmButton: false,
              timer: 3000
          })
           }
        })
        .catch(function (error) {
          console.log(error)
        })
    }, (reason) => {
      console.log("OOOOOOOOOOo",);
      
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  deleteDept(id)
  {
    if(confirm("Are You Sure To Delete This Record"))
    {
    Axios.delete(this.baseService.baseUrl + 'secure/assign/delete_assign/'+id,{
        headers: {
          'x-auth' : this.baseService.authToken
        },
      })
      .then(response => {        
        console.log(response)
        if (response.data.status) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
        .then(() => {
          location.reload(); });
        // this.getDept();
        }
        else{
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: response.data["message"],
            showConfirmButton: false,
            timer: 3000
        })
         }
      })
      .catch(function (error) {
        console.log(error)
      })
    }

  }


}
